.backHome {
  display: flex;
  justify-content: space-between;
  margin: 3rem;

  .clickButton {
    padding: 1rem;
    cursor: pointer;
    font-size: 1.125rem;
    font-family: $font-base;
    font-weight: 300;
    border: 0.75px solid $buttonBorder;

    &:hover {
      opacity: 0.5;
    }
  }
}
